import { Component, ViewChild } from '@angular/core';
import { DatePipe } from '@angular/common';
import { NavController, NavParams, LoadingController, AlertController, ModalController, ViewController, PopoverController, App } from 'ionic-angular';
import * as _ from 'lodash';
import { Storage } from '@ionic/storage';

import { ShoppingcartPage } from '../../pages/shoppingcart/shoppingcart';
import { ChatdetailPage } from '../../pages/chatdetail/chatdetail';
import { ProductratingsPage } from '../../pages/productratings/productratings';
import { ShopprofilePage } from '../../pages/shopprofile/shopprofile';
import { ProductcommentPage } from '../../pages/productcomment/productcomment';
import { LoginPage } from '../../pages/login/login';

@Component({
  template: `
    <ion-list>
      <ion-item (click)="viewChat()"><ion-icon ios="ios-chatbubbles-outline" md="ios-chatbubbles-outline" item-start></ion-icon>Chat Now</ion-item>
      <ion-item (click)="close()"><ion-icon ios="ios-share-outline" md="ios-share-outline" item-start></ion-icon>Share</ion-item>
      <ion-item (click)="close()"><ion-icon ios="ios-home-outline" md="ios-home-outline" item-start></ion-icon>Back to Home</ion-item>
      <ion-item (click)="close()"><ion-icon ios="ios-information-circle-outline" md="ios-information-circle-outline" item-start></ion-icon>Report this product</ion-item>
    </ion-list>
  `
})
export class DetailPopoverPage {
  user: any = {};
  constructor(public viewCtrl: ViewController, public navCtrl: NavController, public modalCtrl: ModalController) {
    this.user = JSON.parse(localStorage.getItem('app.user')) || {};
  }

  close() {
    this.viewCtrl.dismiss();
  }

  viewChat() {
    // TODO: Check if Authenticated
    if (_.isEmpty(this.user)) {
      let modal = this.modalCtrl.create(LoginPage);
      modal.present();
      return;
    }

    this.navCtrl.push(ChatdetailPage, {
      user: {
        "Id": "210000198410281948",
        "name": "Cassio Zen",
        "email": "cassiozen@gmail.com"
      }
    });
    this.viewCtrl.dismiss();
  }
}
@Component({
  selector: 'page-details',
  templateUrl: 'details.html',
})
export class DetailsPage {
  user: any = {};
  constructor(public app: App, public navCtrl: NavController, public navParams: NavParams, public loadingCtrl: LoadingController, public storage: Storage, public alertCtrl: AlertController, public modalCtrl: ModalController, public popoverCtrl: PopoverController) {
    this.user = JSON.parse(localStorage.getItem('app.user')) || {};
  }

  ionViewDidLoad() {

  }

  doRefresh(refresher) {
    setTimeout(() => {
      refresher.complete();
    }, 1000);
  }

  likeItem() {

  }

  presentPopover(myEvent) {
    let popover = this.popoverCtrl.create(DetailPopoverPage);
    popover.present({
      ev: myEvent
    });
  }

  viewCart() {
    // TODO: Check if Authenticated
    if (_.isEmpty(this.user)) {
      let modal = this.modalCtrl.create(LoginPage);
      modal.present();
      return;
    }

    let modal = this.modalCtrl.create(ShoppingcartPage);
    modal.present();
  }

  viewProductRating() {
    if (_.isEmpty(this.user)) {
      let modal = this.modalCtrl.create(LoginPage);
      modal.present();
      return;
    }

    this.navCtrl.push(ProductratingsPage);
  }

  viewShopProfile() {
    this.navCtrl.push(ShopprofilePage);
  }

  viewProductComment() {
    if (_.isEmpty(this.user)) {
      let modal = this.modalCtrl.create(LoginPage);
      modal.present();
      return;
    }
    this.navCtrl.push(ProductcommentPage);
  }

  gotoDetails() {
    this.app.getRootNav().push(DetailsPage);
  }

  viewChat() {
    // TODO: Check if Authenticated
    if (_.isEmpty(this.user)) {
      let modal = this.modalCtrl.create(LoginPage);
      modal.present();
      return;
    }

    this.navCtrl.push(ChatdetailPage, {
      user: {
        "Id": "210000198410281948",
        "name": "Cassio Zen",
        "email": "cassiozen@gmail.com"
      }
    });
  }

  addToCart() {
    if (_.isEmpty(this.user)) {
      let modal = this.modalCtrl.create(LoginPage);
      modal.present();
      return;
    }
  }

  buyNow() {
    if (_.isEmpty(this.user)) {
      let modal = this.modalCtrl.create(LoginPage);
      modal.present();
      return;
    }
  }
}



