import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, AlertController, LoadingController, Events } from 'ionic-angular';
import * as _ from 'lodash';
import { Socket } from 'ng-socket-io';

import { AuthProvider } from '../../providers/service/authentication';

import { ForgotPage } from '../../pages/forgot/forgot';

@IonicPage()
@Component({
	selector: 'page-login',
	templateUrl: 'login.html',
})
export class LoginPage {
	user: any = {};

	constructor(public navCtrl: NavController, public navParams: NavParams, public viewCtrl: ViewController, public auth: AuthProvider, public alertCtrl: AlertController, public loadingCtrl: LoadingController, public events: Events,
		private socket: Socket) {
	}

	close() {
		this.viewCtrl.dismiss();
	}

	forgot() {
		console.log('forgot: ');
		this.navCtrl.push(ForgotPage);
	}

	login() {
		console.log('this.user: ', this.user);
		if (_.isEmpty(this.user)) {
			let alert = this.alertCtrl.create({
				title: 'Invalid Username',
				subTitle: 'Invalid username and password!',
				buttons: ['OK']
			});
			alert.present();
			return;
		}

		if (_.isEmpty(this.user.username)) {
			let alert = this.alertCtrl.create({
				title: 'Invalid Username',
				subTitle: 'Invalid username and password!',
				buttons: ['OK']
			});
			alert.present();
			return;
		} else if (_.isEmpty(this.user.password)) {
			let alert = this.alertCtrl.create({
				title: 'Invalid Password',
				subTitle: 'Invalid username and password!',
				buttons: ['OK']
			});
			alert.present();
			return;
		}

		let loading = this.loadingCtrl.create();
		loading.present();
		this.auth.authenticate(this.user).then((data: any) => {
			console.log('data: ', data);
			if (data && data.success) {
				localStorage.setItem('app.user', JSON.stringify(data.result.user));
				localStorage.setItem('app.token', data.result.token);

				this.events.publish('app.login');

				this.socket.connect();
				this.socket.emit('create-room', this.user.uuid);

				loading.dismiss();
				setTimeout(() => {
					this.viewCtrl.dismiss('login');
				}, 1000);
			} else if (data && !data.success) {
				loading.dismiss();

				let alert = this.alertCtrl.create({
					title: 'Error',
					subTitle: data.msg,
					buttons: ['OK']
				});
				alert.present();
				return;
			}
		}, (error) => {
			console.log('error: ', error);
			loading.dismiss();
		})
	}

}



