import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, Tabs, App, AlertController, LoadingController, Events } from 'ionic-angular';
import * as _ from 'lodash';
import { Socket } from 'ng-socket-io';

import { ShoppingcartPage } from '../../pages/shoppingcart/shoppingcart';
import { ChatPage } from '../../pages/chat/chat';
import { MyratingPage } from '../../pages/myrating/myrating';
import { AccountsettingsPage } from '../../pages/accountsettings/accountsettings';
import { MywalletPage } from '../../pages/mywallet/mywallet';
import { MypurchasesPage } from '../../pages/mypurchases/mypurchases';
import { MylikesPage } from '../../pages/mylikes/mylikes';

import { LoginPage } from '../../pages/login/login';
import { SignupPage } from '../../pages/signup/signup';
import { AboutPage } from '../../pages/about/about';

import { TabsPage } from '../../pages/tabs/tabs';


import { UsersServiceProvider } from '../../providers/service/users';

@IonicPage()
@Component({
	selector: 'page-myprofile',
	templateUrl: 'myprofile.html',
})
export class MyprofilePage {
	imageUrl: string = 'assets/imgs/bg.jpg';
	user: any = {};
	mode: string = 'buyer';
	showProfile: boolean = false;
	isSeller: boolean = false;
	isApplyAsSeller: boolean = false;

	constructor(public app: App, public navCtrl: NavController, public navParams: NavParams, public modalCtrl: ModalController, public alertCtrl: AlertController, public loadingCtrl: LoadingController, public users: UsersServiceProvider, private socket: Socket, public events: Events) {
		console.log('ionViewCanEnter constructor');
		this.getCurrentUser();

		this.events.subscribe('approve-seller', () => {
			console.log('approve-seller');
			this.isSeller = false;
		});
	}

	ionViewWillEnter() {
		console.log('ionViewWillEnter MyprofilePage');
		this.getCurrentUser();
	}

	getCurrentUser() {
		this.user = JSON.parse(localStorage.getItem('app.user')) || {};
		if (!_.isEmpty(this.user)) {
			this.showProfile = true;

			if (this.user.isSeller) {
				this.isSeller = true;
			} else {
				this.isSeller = false;
			}

			if (!_.isEmpty(this.user.sellerApprovalCode)) {
				this.isApplyAsSeller = true;
			} else {
				this.isApplyAsSeller = false;
			}

			if (!this.socket.ioSocket.connected) {
				this.socket.connect();
				this.socket.emit('create-room', this.user.uuid);
			} else {
				this.socket.emit('create-room', this.user.uuid);
			}
		} else {
			this.showProfile = false;
		}
	}

	doRefresh(ev) {
		if (!_.isEmpty(this.user)) {
			this.users.getCurrentUser().then((data: any) => {
				if (data && data.success) {
					localStorage.setItem('app.user', JSON.stringify(data.result));
				}
				ev.complete();
			}, (error) => {
				console.log('error: ', error);
				ev.complete();
			});
		} else {
			ev.complete();
		}
	}

	openLogin() {
		// TODO: Check for authentication
		let modal = this.modalCtrl.create(LoginPage);
		modal.onDidDismiss((res) => {
			if (res == 'login') {
				this.getCurrentUser();
			}
		});
		modal.present();
	}

	openSignUp() {
		// TODO: Check for authentication
		console.log('openSignUp')
		let modal = this.modalCtrl.create(SignupPage);
		modal.present();
	}


	viewCart() {
		// TODO: Check if Authenticated
		// this.navCtrl.push(ShoppingcartPage);

		if (_.isEmpty(this.user)) {
			this.openLogin();
			return;
		}

		this.app.getRootNav().push(ShoppingcartPage);

	}

	viewChat() {
		if (_.isEmpty(this.user)) {
			this.openLogin();
			return;
		}
		let modal = this.modalCtrl.create(ChatPage);
		modal.present();
	}

	viewAccountSettings() {
		if (_.isEmpty(this.user)) {
			this.openLogin();
			return;
		}

		// this.navCtrl.push(AccountsettingsPage);
		this.app.getRootNav().push(AccountsettingsPage);
	}

	viewMyRating(mode) {
		// this.navCtrl.push(MyratingPage);
		this.app.getRootNav().push(MyratingPage,{
			mode: mode
		});
	}

	viewMyPurchases() {
		if (_.isEmpty(this.user)) {
			this.openLogin();
			return;
		}
		this.app.getRootNav().push(MypurchasesPage);
	}

	viewMyWallet() {
		// this.navCtrl.push(MywalletPage);
		this.app.getRootNav().push(MywalletPage);
	}

	viewMyLikes() {
		this.app.getRootNav().push(MylikesPage);
	}

	viewAbout() {
		this.navCtrl.push(AboutPage);
	}

	applyAsSeller() {
		let ctrl = this;

		function applySeller() {
			let loader = ctrl.loadingCtrl.create();
			loader.present();
			ctrl.users.applyAsSeller().then((data: any) => {
				if (data && data.success) {
					loader.dismiss();
					let alert = ctrl.alertCtrl.create({
						title: 'Success!',
						subTitle: data.msg,
						buttons: ['OK']
					});
					alert.present();
				} else if (data && !data.success) {
					loader.dismiss();
					let alert = ctrl.alertCtrl.create({
						title: 'Warning!',
						subTitle: data.msg,
						buttons: ['OK']
					});
					alert.present();
				}
			}, (error: any) => {
				loader.dismiss();
				console.log('error: ', error);
			});
		}

		let confirm = this.alertCtrl.create({
			title: 'Apply as a Seller?',
			message: 'Applying as a seller will you give you an opportunity to sell your preloved items and other stuffs you no longer used. Otherwise you can sell brand new items.',
			buttons: [
				{
					text: 'No',
					handler: () => {
						console.log('Disagree clicked');
					}
				},
				{
					text: 'Proceed',
					handler: () => {
						applySeller();
					}
				}
			]
		});
		confirm.present();
	}

}



