import { Component } from '@angular/core';
import { NavController, LoadingController, AlertController, App, ModalController } from 'ionic-angular';
import * as _ from 'lodash';
import * as async from 'async';
import { Storage } from '@ionic/storage';
import { Socket } from 'ng-socket-io';

import { DetailsPage } from '../../pages/details/details';
import { ShoppingcartPage } from '../../pages/shoppingcart/shoppingcart';
import { ChatPage } from '../../pages/chat/chat';
import { SearchPage } from '../../pages/search/search';
import { CategoriesPage } from '../../pages/categories/categories';
import { BrandsPage } from '../../pages/brands/brands';
import { LoginPage } from '../../pages/login/login';
import { SearchresultPage } from '../../pages/searchresult/searchresult';
import { ShopprofilePage } from '../../pages/shopprofile/shopprofile';


import { CategoriesServiceProvider } from '../../providers/service/categories';
import { BrandsServiceProvider } from '../../providers/service/brands';
import { ServiceProvider } from '../../providers/service/service';
import { ProductsServiceProvider } from '../../providers/service/products';

@Component({
	selector: 'page-home',
	templateUrl: 'home.html'
})
export class HomePage {

	tickerArr: any[] = [];
	tickerArrCopy: any[] = [];
	items: any[] = [];
	favorites: any[] = [];
	categoriesArr: any[] = [];
	brandsArr: any[] = [];
	bannersArr: any[] = [];
	productsArr: any[] = [];

	actions: string = 'top';
	global: any = {};
	user: any = {};

	showList: boolean = false;

	constructor(public app: App, public navCtrl: NavController, public loadingCtrl: LoadingController, public storage: Storage, public alertCtrl: AlertController, public modalCtrl: ModalController,
		public categories: CategoriesServiceProvider, public brands: BrandsServiceProvider, private socket: Socket, public services: ServiceProvider, public products: ProductsServiceProvider) {

		this.user = JSON.parse(localStorage.getItem('app.user')) || {};

		console.log('this.user: ', this.user);
		if (this.user) {
			this.socket.connect();
			this.socket.emit('create-room', this.user.uuid);
		}

		storage.get('favorites').then((val) => {
			if (!_.isEmpty(val)) {
				this.favorites = val;
			}
		});

		this.categories.getAllCategories().then((data: any) => {
			console.log('categories: ', data);
			if (data && data.success) {
				this.categoriesArr = data.result;
			}
		}, (error) => {
			console.log('category error: ', error);
		});

		this.brands.getAllBrands().then((data: any) => {
			console.log('brands: ', data);
			if (data && data.success) {
				this.brandsArr = data.result;
			}
		}, (error) => {
			console.log('brands error: ', error);
		});

		this.services.getHomeBanners().then((data: any) => {
			console.log('banners: ', data);
			if (data && data.success) {
				this.bannersArr = data.result;
			}
		}, (error) => {
			console.log('banners error: ', error);
		});

		this.products.getAllProducts().then((data: any) => {
			if (data && data.success) {
				console.log('products: ', data);
				this.productsArr = data.result;
			}
		}, (error) => {
			console.log('products error: ', error);
		});
	}

	ionViewWillEnter() {
		this.showList = false;
	}

	ionViewDidLoad() {
		this.items = [
			"macbook pro",
			"macbook pro case",
			"macbook pro laptops",
			"macbook pro 13",
			"macbook pro 15",
			"macbook pro retina",
			"macbook pro charger",
			"macbook pro 2016",
			"macbook pro cover",
			"macbook pro sleeve",
			"macbook pro 2012",
			"macbook pro cable",
			"macbook pro 2017",
		];
	}


	checkBlur() {
		// this.showList = false;
	}

	checkFocus() {
		this.showList = true;
	}

	onCancel() {
		this.showList = false;
	}

	gotoSearch() {
		console.log('gotoSearch')
		this.navCtrl.push(SearchPage);
	}

	doRefresh(refresher) {
		async.parallel([
			(callback) => {
				this.categories.getAllCategories().then((data: any) => {
					if (data && data.success) {
						this.categoriesArr = data.result;
						callback();
					}
				}, (error) => {
					callback();
				});
			},
			(callback) => {
				this.brands.getAllBrands().then((data: any) => {
					if (data && data.success) {
						this.brandsArr = data.result;
						callback();
					}
				}, (error) => {
					callback();
				});
			},
			(callback) => {
				this.services.getHomeBanners().then((data: any) => {
					console.log('banners: ', data);
					if (data && data.success) {
						this.bannersArr = data.result;
						callback();
					}
				}, (error) => {
					callback();
				});
			},
			(callback) => {
				this.products.getAllProducts().then((data: any) => {
					if (data && data.success) {
						console.log('products: ', data);
						this.productsArr = data.result;
						callback();
					}
				}, (error) => {
					console.log('products error: ', error);
					callback();
				});
			}
		], () => {
			refresher.complete();
		})
	}

	getItems(ev: any) {
		let val = ev.target.value;
		console.log('val: ', val)
		if (val && val.trim() != '') {
			this.tickerArr = _.filter(this.tickerArrCopy, (row: any) => {
				return row.name == val;
			})
		} else {
			this.tickerArr = this.tickerArrCopy;
		}
	}

	gotoDetails(item) {
		/* this.navCtrl.push(DetailsPage, {
			crypto: item
		}); */
		this.app.getRootNav().push(DetailsPage, {
			crypto: item
		});
	}

	addToFav(item) {

	}

	segmentChanged(ev) {

	}

	viewCart() {
		// TODO: Check if Authenticated
		if (_.isEmpty(this.user)) {
			let modal = this.modalCtrl.create(LoginPage);
			modal.present();
			return;
		}

		this.app.getRootNav().push(ShoppingcartPage);
	}

	viewChat() {
		if (_.isEmpty(this.user)) {
			let modal = this.modalCtrl.create(LoginPage);
			modal.present();
			return;
		}

		let modal = this.modalCtrl.create(ChatPage);
		modal.present();
	}

	viewCategories() {
		this.app.getRootNav().push(CategoriesPage);
	}

	viewBrands() {
		this.app.getRootNav().push(BrandsPage);
	}

	viewResults(item, type) {
		console.log('item: ', item);
		this.app.getRootNav().push(SearchresultPage, {
			type: type,
			item: item
		});
	}

	viewShop() {
		this.app.getRootNav().push(ShopprofilePage);
	}
}



