import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, PopoverController, ViewController,App,MenuController,Content } from 'ionic-angular';

import { DetailsPage } from '../../pages/details/details';

@Component({
	template: `
    <ion-list>
      <button ion-item (click)="close()">Best Match</button>
      <button ion-item (click)="close()">Price (Low to High)</button>
      <button ion-item (click)="close()">Price (High to Low)</button>
      <button ion-item (click)="close()">Number of Orders</button>
      <button ion-item (click)="close()">Top Sales</button>
      <button ion-item (click)="close()">Date Added</button>
    </ion-list>
  `
})
export class SearchPopoverPage {
	constructor(public viewCtrl: ViewController) { }

	close() {
		this.viewCtrl.dismiss();
	}
}
@IonicPage()
@Component({
	selector: 'page-search',
	templateUrl: 'search.html',
})
export class SearchPage {
	items: any[] = [];
	showList: boolean = false;
	isList: boolean = true;

	@ViewChild(Content) content: Content;
	constructor(public app: App,public navCtrl: NavController, public navParams: NavParams, public popoverCtrl: PopoverController, public menuCtrl: MenuController) {
		this.items = [
			"macbook pro",
			"macbook pro case",
			"macbook pro laptops",
			"macbook pro 13",
			"macbook pro 15",
			"macbook pro retina",
			"macbook pro charger",
			"macbook pro 2016",
			"macbook pro cover",
			"macbook pro sleeve",
			"macbook pro 2012",
			"macbook pro cable",
			"macbook pro 2017",
		];
	}

	presentPopover(myEvent) {
		let popover = this.popoverCtrl.create(SearchPopoverPage);
		popover.present({
			ev: myEvent
		});
	}

	ionViewDidLoad() {
		console.log('ionViewDidLoad SearchPage');
	}

	ionViewWillLeave(){
		this.menuCtrl.enable(false,'filtermenu');
	}

	onInput(ev) {

	}

	onCancel(ev) {
		this.showList = false;
		this.content.resize();
	}

	checkBlur() {
		// this.showList = false;
	}

	checkFocus() {
		this.showList = true;
		this.content.resize();
	}

	viewFilters() {
		console.log('viewFilters');
		this.menuCtrl.enable(true,'filtermenu');
		this.menuCtrl.toggle('right');
	}

	checkDisplay(display){
		this.isList = (display === 'list') ? true : false;
	}

	gotoDetails() {
		this.app.getRootNav().push(DetailsPage);
	}
}



