import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, ActionSheetController, LoadingController } from 'ionic-angular';
import { Socket } from 'ng-socket-io';

import { ChatPage } from '../../pages/chat/chat';
import { MyaddressPage } from '../../pages/myaddress/myaddress';
import { UserprofilePage } from '../../pages/userprofile/userprofile';
import { MybankcardsPage } from '../../pages/mybankcards/mybankcards';
import { AboutPage } from '../../pages/about/about';

import { TabsPage } from '../../pages/tabs/tabs';


@IonicPage()
@Component({
  selector: 'page-accountsettings',
  templateUrl: 'accountsettings.html',
})
export class AccountsettingsPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, public modalCtrl: ModalController, public actionSheetCtrl: ActionSheetController, public loadingCtrl: LoadingController, private socket: Socket) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AccountsettingsPage');
  }

  viewChat() {
		let modal = this.modalCtrl.create(ChatPage);
		modal.present();
	}

	viewMyAddressess() {
		this.navCtrl.push(MyaddressPage);
	}

  viewProfile() {
    this.navCtrl.push(UserprofilePage);
  }

  viewBankCards() {
    this.navCtrl.push(MybankcardsPage);
  }

  viewAbout() {
    this.navCtrl.push(AboutPage);
  }

  logout() {
    let actionSheet = this.actionSheetCtrl.create({
      title: 'Your profile will be saved. Can\'t wait to have you back',
      buttons: [
        {
          text: 'Logout',
          role: 'destructive',
          handler: () => {
            let loader = this.loadingCtrl.create({
              duration: 1000
            });
            localStorage.removeItem('app.user');
            localStorage.removeItem('app.token');
            this.socket.disconnect()
            loader.onDidDismiss(() => {
              this.navCtrl.setRoot(TabsPage, null, { animate: true, direction: 'back' });
            });
            loader.present();
          }
        }, {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        }
      ]
    });
    actionSheet.present();
  }

}



