import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, App,MenuController,PopoverController, ViewController } from 'ionic-angular';

import { DetailsPage } from '../../pages/details/details';
import { ChatdetailPage } from '../../pages/chatdetail/chatdetail';


@Component({
	template: `
    <ion-list>
      <button ion-item (click)="close()">Share</button>
      <button ion-item (click)="close()">Report this user</button>
      <button ion-item (click)="close()">Block this user</button>
    </ion-list>
  `
})
export class ShopProfilePopoverPage {
	constructor(public viewCtrl: ViewController) { }

	close() {
		this.viewCtrl.dismiss();
	}
}
@IonicPage()
@Component({
	selector: 'page-shopprofile',
	templateUrl: 'shopprofile.html',
})
export class ShopprofilePage {
	imageUrl: string = 'assets/imgs/bg.jpg';
	mode: string = 'shop';
	action: string = 'popular';
	isLow: boolean = false;
	constructor(public app: App, public navCtrl: NavController, public navParams: NavParams, public menuCtrl: MenuController,public popoverCtrl: PopoverController) {
	}

	ionViewDidLoad() {
		console.log('ionViewDidLoad ShopprofilePage');
	}

	gotoDetails() {
		this.app.getRootNav().push(DetailsPage);
	}

	sortPrice() {
		this.isLow = !this.isLow;
	}

	viewChat() {
		this.navCtrl.push(ChatdetailPage, {
			user: {
				"Id": "210000198410281948",
				"name": "Cassio Zen",
				"email": "cassiozen@gmail.com"
			}
		});
	}

	viewFilters() {
		this.menuCtrl.enable(true,'filtermenu');
		this.menuCtrl.toggle('right');
	}

	presentPopover(myEvent) {
		let popover = this.popoverCtrl.create(ShopProfilePopoverPage);
		popover.present({
			ev: myEvent
		});
	}
}



