import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,ModalController } from 'ionic-angular';

import { ChatPage } from '../../pages/chat/chat';
import { MybankcardsEntryPage } from '../../pages/mybankcards-entry/mybankcards-entry';

@IonicPage()
@Component({
  selector: 'page-mybankcards',
  templateUrl: 'mybankcards.html',
})
export class MybankcardsPage {

  constructor(public navCtrl: NavController, public navParams: NavParams,public modalCtrl: ModalController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MybankcardsPage');
  }

  viewChat(){
		let modal = this.modalCtrl.create(ChatPage);
		modal.present();
	}

  addNewAccount(){
    this.navCtrl.push(MybankcardsEntryPage);
  }
}



