import { Restangular } from 'ngx-restangular/dist/esm/src';
import { Injectable } from '@angular/core';

@Injectable()
export class ProductsServiceProvider {

    constructor(public restangular: Restangular) { }

    getAllProducts(params?) {
        return new Promise((resolve, reject) => {
            let callbackResponse = (resp: any) => {
                resolve(resp.data);
            };

            let errorResponse = (error) => {
                console.log('error: ', error);
                reject(error);
            };
            this.restangular.all('products').customGET("", params).subscribe(callbackResponse, errorResponse);
        });
    }

    saveProduct(data) {
        return new Promise((resolve, reject) => {
            let callbackResponse = (resp: any) => {
                resolve(resp.data);
            };

            let errorResponse = (error) => {
                console.log('error: ', error);
                reject(error);
            };
            this.restangular.withConfig((config) => {
                config.setDefaultHeaders({ 'Authorization': 'Bearer ' + localStorage.getItem('app.token') });
            }).all('products').customPOST(data).subscribe(callbackResponse, errorResponse);
        });
    }

    saveProductImage(productId, data) {
        var formData = new FormData();
        formData.append('image', data.image);
        return this.restangular.withConfig((config) => {
            config.setDefaultHeaders({ 'Authorization': 'Bearer ' + localStorage.getItem('app.token') });
        }).all('products/' + productId + '/image').customPUT(formData, '', undefined, {
            'Content-Type': undefined
        }).then(function(res) {
            return res;
        }, function(err) {
            return err.data;
        });
    }

    saveProductVariants(productId, data) {
        return new Promise((resolve, reject) => {
            let callbackResponse = (resp: any) => {
                resolve(resp.data);
            };

            let errorResponse = (error) => {
                console.log('error: ', error);
                reject(error);
            };
            this.restangular.withConfig((config) => {
                config.setDefaultHeaders({ 'Authorization': 'Bearer ' + localStorage.getItem('app.token') });
            }).all('products/' + productId + '/variation').customPOST(data).subscribe(callbackResponse, errorResponse);
        });
    }

    saveProductWholesale(productId, data) {
        return new Promise((resolve, reject) => {
            let callbackResponse = (resp: any) => {
                resolve(resp.data);
            };

            let errorResponse = (error) => {
                console.log('error: ', error);
                reject(error);
            };
            this.restangular.withConfig((config) => {
                config.setDefaultHeaders({ 'Authorization': 'Bearer ' + localStorage.getItem('app.token') });
            }).all('products/' + productId + '/wholesale').customPOST(data).subscribe(callbackResponse, errorResponse);
        });
    }

    saveProductShipping(productId, data) {
        return new Promise((resolve, reject) => {
            let callbackResponse = (resp: any) => {
                resolve(resp.data);
            };

            let errorResponse = (error) => {
                console.log('error: ', error);
                reject(error);
            };
            this.restangular.withConfig((config) => {
                config.setDefaultHeaders({ 'Authorization': 'Bearer ' + localStorage.getItem('app.token') });
            }).all('products/' + productId + '/shipping').customPOST(data).subscribe(callbackResponse, errorResponse);
        });
    }
}



