import { Component, ViewChild } from '@angular/core';
import { Platform, Nav, MenuController, Events } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { JwtHelperService } from '@auth0/angular-jwt';
import { Socket } from 'ng-socket-io';
import * as _ from 'lodash';

import { TabsPage } from '../pages/tabs/tabs';
import { MyprofilePage } from '../pages/myprofile/myprofile';


import { UsersServiceProvider } from '../providers/service/users';


@Component({
	templateUrl: 'app.html'
})
export class MyApp {
	rootPage: any = TabsPage;
	user: any = {};
	constructor(platform: Platform, statusBar: StatusBar, splashScreen: SplashScreen, public menuCtrl: MenuController, private socket: Socket, public events: Events, public users: UsersServiceProvider) {
		platform.ready().then(() => {
			statusBar.styleDefault();
			splashScreen.hide();
		});

		const helper = new JwtHelperService();
		let myRawToken = localStorage.getItem('app.token');
		const isExpired = helper.isTokenExpired(myRawToken);
		if (isExpired) {
			localStorage.removeItem('app.user');
			localStorage.removeItem('app.token');
		}

		this.user = JSON.parse(localStorage.getItem('app.user')) || {};
		if (!_.isEmpty(this.user)) {
			if (!this.socket.ioSocket.connected) {
				this.socket.connect();
				this.socket.emit('create-room', this.user.uuid);
			} else {
				this.socket.emit('create-room', this.user.uuid);
			}

			this.socket.on('connect', () => {
				//Add user called nickname
				console.log('reconnect');
				this.socket.emit('create-room', this.user.uuid);
			});

			this.socket.on('approve-seller', () => {
				console.log('publish approve-seller');
				this.users.getCurrentUser().then((data: any) => {
					if (data && data.success) {
						localStorage.removeItem('app.user');
						localStorage.setItem('app.user', JSON.stringify(data.result));
						this.events.publish('approve-seller');
					}
				}, (error) => {
					console.log('error: ', error);
				});
			});
		}

	}



	closeMenu() {
		console.log('closeMenu');
		this.menuCtrl.close();
	}
}



