import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,ModalController } from 'ionic-angular';

import { ChatPage } from '../../pages/chat/chat';
import { MyaddressEntryPage } from '../../pages/myaddress-entry/myaddress-entry';

@IonicPage()
@Component({
  selector: 'page-myaddress',
  templateUrl: 'myaddress.html',
})
export class MyaddressPage {

  constructor(public navCtrl: NavController, public navParams: NavParams,public modalCtrl:ModalController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MyaddressPage');
  }

  viewChat(){
		let modal = this.modalCtrl.create(ChatPage);
		modal.present();
	}

	addNewAddress(){
		this.navCtrl.push(MyaddressEntryPage);
	}

}



