import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, PopoverController, ViewController, Events, Content, TextInput } from 'ionic-angular';
import { ChatService, ChatMessage, UserInfo } from "../../providers/service/chat";


@Component({
    template: `
    <ion-list>
      <ion-item (click)="close()"><ion-icon name="contact" item-start></ion-icon> View Profile</ion-item>
      <ion-item (click)="close()"><ion-icon name="information-circle" item-start></ion-icon> Report this user</ion-item>
      <ion-item (click)="close()"><ion-icon name="remove-circle" item-start></ion-icon> Block this user</ion-item>
    </ion-list>
  `
})
export class ChatDetailPopoverPage {
    constructor(public viewCtrl: ViewController) { }

    close() {
        this.viewCtrl.dismiss();
    }
}
@IonicPage()
@Component({
    selector: 'page-chatdetail',
    templateUrl: 'chatdetail.html',
})
export class ChatdetailPage {
    user: any;
    userinfo : UserInfo;
    toUser: UserInfo;

    @ViewChild(Content) content: Content;
    @ViewChild('chat_input') messageInput: TextInput;
    msgList: ChatMessage[] = [];
    editorMsg = '';
    showEmojiPicker = false;
    constructor(public navCtrl: NavController, public navParams: NavParams, public popoverCtrl: PopoverController, private chatService: ChatService, private events: Events) {
        this.user = navParams.get('user');
        this.toUser = {
            id: this.user.Id,
            name: this.user.name
        };
        console.log('this.toUser: ', this.toUser);
        // Get mock user information
        this.chatService.getUserInfo()
            .then((res) => {
                this.userinfo = res;
                console.log('this.userinfo: ', this.userinfo);
            });
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad ChatdetailPage');
    }

    ionViewWillLeave() {
        // unsubscribe
        this.events.unsubscribe('chat:received');
    }

    ionViewDidEnter() {
        //get message list
        this.getMsg();

        // Subscribe to received  new message events
        this.events.subscribe('chat:received', msg => {
            this.pushNewMsg(msg);
        })
    }

    onFocus() {
        this.showEmojiPicker = false;
        this.content.resize();
        this.scrollToBottom();
    }

    switchEmojiPicker() {
        this.showEmojiPicker = !this.showEmojiPicker;
        if (!this.showEmojiPicker) {
            this.messageInput.setFocus();
        }
        this.content.resize();
        this.scrollToBottom();
    }

    /**
     * @name getMsg
     * @returns {Promise<ChatMessage[]>}
     */
    private getMsg() {
        // Get mock message list
        return this.chatService
            .getMsgList()
            .subscribe(res => {
                this.msgList = res;
                console.log('this.msgList: ', this.msgList);
                this.scrollToBottom();
            });
    }

    /**
     * @name sendMsg
     */
    sendMsg() {
        if (!this.editorMsg.trim()) return;

        // Mock message
        const id = Date.now().toString();
        let newMsg: ChatMessage = {
            messageId: Date.now().toString(),
            userId: this.user.id,
            userName: this.user.name,
            userAvatar: this.user.avatar,
            toUserId: this.toUser.id,
            time: Date.now(),
            message: this.editorMsg,
            status: 'pending'
        };

        this.pushNewMsg(newMsg);
        this.editorMsg = '';

        if (!this.showEmojiPicker) {
            this.messageInput.setFocus();
        }

        this.chatService.sendMsg(newMsg)
            .then(() => {
                let index = this.getMsgIndexById(id);
                if (index !== -1) {
                    this.msgList[index].status = 'success';
                }
            })
    }

    /**
     * @name pushNewMsg
     * @param msg
     */
    pushNewMsg(msg: ChatMessage) {
        const userId = this.user.id,
            toUserId = this.toUser.id;
        // Verify user relationships
        if (msg.userId === userId && msg.toUserId === toUserId) {
            this.msgList.push(msg);
        } else if (msg.toUserId === userId && msg.userId === toUserId) {
            this.msgList.push(msg);
        }
        this.scrollToBottom();
    }

    getMsgIndexById(id: string) {
        return this.msgList.findIndex(e => e.messageId === id)
    }

    scrollToBottom() {
        setTimeout(() => {
            if (this.content.scrollToBottom) {
                this.content.scrollToBottom();
            }
        }, 400)
    }

    more(myEvent) {
        let popover = this.popoverCtrl.create(ChatDetailPopoverPage);
        popover.present({
            ev: myEvent
        });
    }
}



