import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import * as _ from 'lodash';

import { BrandsServiceProvider } from '../../providers/service/brands';

@IonicPage()
@Component({
	selector: 'page-brands',
	templateUrl: 'brands.html',
})
export class BrandsPage {
	brand: any = {};
	brandsArr: any[] = [];

	isProduct: boolean = false;
	callback: any;

	constructor(public navCtrl: NavController, public navParams: NavParams, public brands: BrandsServiceProvider) {
		if (this.navParams.get('isProduct')) {
			this.isProduct = this.navParams.get('isProduct');
			this.callback = this.navParams.get('callback');
		}
	}

	ionViewDidLoad() {
		console.log('ionViewDidLoad BrandsPage');
		this.brands.getAllBrands().then((data: any) => {
			console.log('categories: ', data);
			if (data && data.success) {
				this.brandsArr = data.result;
				_.each(this.brandsArr, (row: any) => {
					row.opened = false;
				});
			}
		});
	}

	doRefresh(ev) {
		console.log('ionViewDidLoad CategoriesPage');
		this.brands.getAllBrands().then((data: any) => {
			console.log('categories: ', data);
			if (data && data.success) {
				this.brandsArr = data.result;
				ev.complete();
			}
		});
	}

	viewBrands(item) {
		console.log('viewBrands');
		this.brand = item;
		if (!this.isProduct) {
			
		}else{
			this.callback(this.brand).then(() => { this.navCtrl.pop() });
		}
	}

}



