import { Component, ViewChild, NgZone } from '@angular/core';
import { IonicPage, NavController, NavParams, App, MenuController, Content } from 'ionic-angular';
import * as $ from "jquery";
import 'slick-carousel/slick/slick';

import { DetailsPage } from '../../pages/details/details';
import { ShopprofilePage } from '../../pages/shopprofile/shopprofile';

import { CategoriesServiceProvider } from '../../providers/service/categories';

@IonicPage()
@Component({
	selector: 'page-searchresult',
	templateUrl: 'searchresult.html',
})
export class SearchresultPage {
	items: any = {};
	showList: boolean = false;
	isList: boolean = true;
	showHeader: boolean = false;

	categoriesArr: any[] = [];
	searches: any[] = [];

	type: string;
	mode: string = 'popular';

	@ViewChild(Content) content: Content;
	constructor(public app: App, public navCtrl: NavController, public navParams: NavParams, public menuCtrl: MenuController, public zone: NgZone, public categories: CategoriesServiceProvider) {
		this.items = navParams.get('item');
		this.type = navParams.get('type');

		this.showHeader = false;

		this.showList = false;
	}

	ngOnInit() {
		console.log('ngOnInit')
		
	}


	ngAfterViewInit() {
		console.log('ngAfterViewInit')
		let ctrl = this;

		$('.myCarousel').slick({
			dots: false,
			centerMode: false,
			infinite: false,
			slidesToShow: 3
		});

		this.content.ionScroll.subscribe(($event) => {
			// console.log('$event.scrollTop: ', $event.scrollTop);
			if ($event.scrollTop >= 530) {
				ctrl.zone.run(() => {
					ctrl.showHeader = true;
				});
				ctrl.content.resize();
			} else {
				ctrl.zone.run(() => {
					ctrl.showHeader = false;
				});
				ctrl.content.resize();
			}
		});

	}


	ionViewDidLoad() {
		console.log('ionViewDidLoad SearchresultPage');
		this.searches = [
			"macbook pro",
			"macbook pro case",
			"macbook pro laptops",
			"macbook pro 13",
			"macbook pro 15",
			"macbook pro retina",
			"macbook pro charger",
			"macbook pro 2016",
			"macbook pro cover",
			"macbook pro sleeve",
			"macbook pro 2012",
			"macbook pro cable",
			"macbook pro 2017",
		];

		if (this.type == 'category') {
			this.categories.getAllSubCategories(this.items.categoryId).then((data: any) => {
				if (data && data.success) {
					this.categoriesArr = data.result;
					console.log('this.categoriesArr: ', this.categoriesArr);

					if (this.categoriesArr) {
						$('.myCategories').slick({
							dots: false,
							centerMode: false,
							infinite: false,
							slidesToShow: 4
						});
					}
				}
			});
		}
	}


	onCancel(ev) {
		this.showList = false;
		$('.myCarousel').slick({
			dots: false,
			centerMode: false,
			infinite: false,
			slidesToShow: 3
		});
		this.content.resize();
	}

	checkBlur() {
		// this.showList = false;
	}

	checkFocus() {
		this.showList = true;
		this.content.resize();
	}

	viewFilters() {
		console.log('viewFilters');
		this.menuCtrl.enable(true, 'filtermenu');
		this.menuCtrl.toggle('right');
	}

	gotoDetails(item) {
		this.navCtrl.push(DetailsPage);
	}

	viewShop(){
		this.navCtrl.push(ShopprofilePage);
	}
}



