import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, App, Tabs, ModalController, Events } from 'ionic-angular';
import * as _ from 'lodash';
import { Socket } from 'ng-socket-io';

import { HomePage } from '../../pages/home/home';
import { AboutPage } from '../../pages/about/about';
import { FeedPage } from '../../pages/feed/feed';
import { SettingsPage } from '../../pages/settings/settings';
import { FavoritesPage } from '../../pages/favorites/favorites';
import { MyprofilePage } from '../../pages/myprofile/myprofile';
import { NotificationPage } from '../../pages/notification/notification';

import { LoginPage } from '../../pages/login/login';

@IonicPage()
@Component({
  selector: 'page-tabs',
  templateUrl: 'tabs.html',
})
export class TabsPage {

  tab1Root = HomePage;
  tab2Root = FeedPage;
  tab3Root = FavoritesPage;
  tab4Root = NotificationPage;
  tab5Root = MyprofilePage;

  user: any = {};
  mapTabEnabled: boolean = true;
  notifCount: number = 0;

  constructor(public navCtrl: NavController, public navParams: NavParams, public app: App, public modalCtrl: ModalController, public events: Events, private socket: Socket) {
    this.user = JSON.parse(localStorage.getItem('app.user')) || {};
    if (_.isEmpty(this.user)) {
      this.mapTabEnabled = false;
    } else {
      this.mapTabEnabled = true;
    }

    this.events.subscribe('app.login', () => {
      this.mapTabEnabled = true;
    });

    this.events.subscribe('approve-seller', () => {
      console.log('approve-seller');
      this.notifCount = this.notifCount + 1;
    });
  }

  myMethod($event) {
    this.user = JSON.parse(localStorage.getItem('app.user')) || {};
    if (_.isEmpty(this.user)) {
      this.mapTabEnabled = false;
    } else {
      this.mapTabEnabled = true;
    }
  }

}



