import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController,ToastController,LoadingController,AlertController } from 'ionic-angular';

import { UsersServiceProvider } from '../../providers/service/users';
import { TabsPage } from '../../pages/tabs/tabs';

@IonicPage()
@Component({
	selector: 'page-signup',
	templateUrl: 'signup.html',
})
export class SignupPage {
	user: any = {};
	errArr:any[] = [];

	constructor(public navCtrl: NavController, public navParams: NavParams, public viewCtrl: ViewController, public users: UsersServiceProvider,public toastCtrl: ToastController, public loadingCtrl: LoadingController,
		public alertCtrl: AlertController) {
	}

	ionViewDidLoad() {
		console.log('ionViewDidLoad SignupPage');
	}

	save() {
		let loading = this.loadingCtrl.create();
		loading.present();
		this.users.signUpUser(this.user).then((data:any)=>{
			if(data && data.success){
				console.log('data: ',data);
				loading.dismiss();

				let alert = this.alertCtrl.create({
					title: 'Success',
					subTitle: data.msg,
					buttons: ['OK']
				});
				alert.onDidDismiss(()=>{
					this.navCtrl.setRoot(TabsPage, null, { animate: true, direction: 'back' });
				});
				alert.present();
			}else{
				loading.dismiss();

				let alert = this.alertCtrl.create({
					title: 'Error',
					subTitle: data.msg,
					buttons: ['OK']
				});
				alert.present();
				return;
			}
		},(error)=>{
			loading.dismiss();
			console.log('error: ',error);
			let err = error.data;
			this.errArr = err.result;
			setTimeout(()=>{
				this.errArr = [];
			},3000);
		});
	}

	close() {
		this.viewCtrl.dismiss();
	}
}



